import React from 'react';
import { useTranslation } from 'react-i18next';
import {
  Box, Text, HStack
} from '@chakra-ui/react';
import { useNavigate } from 'react-router-dom';

import { shallowEqual } from 'react-redux';
import style from './PaymentSuccess.module.css';
import MiniBox from '../../../layout/MiniBox';
import Status from '../../../components/Status';
import { useAppSelector } from '../../../hooks/store';
import { sendMessage } from '../../../utils/sdk';
import { useModuleData } from '../../../hooks/module';
import { Data } from '../../../modules/Payments';
import { camel2title } from '../../../utils/general';
import { PAYMENTS } from '../../../const/messages';

function PaymentSuccess() {
  const { t } = useTranslation();
  const navigate = useNavigate();

  const paymentDetails = useAppSelector((store) => store.payment.paymentResponse, shallowEqual);
  const contactDetails = useAppSelector((store) => store.contact.selectedContact, shallowEqual);
  const account = useAppSelector((store) => store.account.selectedAccount, shallowEqual);
  const {
    startScreen = 'options',
  } = useModuleData<Data<'intrabank'>>();

  return (
    <div className="page">
      <MiniBox>
        <Status
          status="success"
          title={`$${paymentDetails?.amount} Transferred`}
          ctaText={t('payment_success.ctaText')}
          onNext={() => {
            if (paymentDetails) {
              sendMessage(PAYMENTS.PAYMENT_SUCCESS, {
                transferType: paymentDetails.transferType,
                amount: paymentDetails.amount
              });
            }
            if (startScreen === 'options') {
              navigate('/payments/types');
            }
          }}
        >
          <Box>
            <Text variant="regular" className={`${style.transfer_initiated} text-light`} pt="16px">
              {t(`payment_success.${paymentDetails?.transferType}_info`, {
                to: contactDetails?.name
              })}

            </Text>
            <Box className={style.card_info}>
              <HStack className={style.card_info_item}>
                <Text w="50%" variant="regular" className="text-light" textAlign="left">{t('payment_success.source')}</Text>
                <Text w="50%" variant="regular" textAlign="left">{account?.label}</Text>
              </HStack>
              <HStack className={style.card_info_item}>
                <Text w="50%" variant="regular" className="text-light" textAlign="left">{t('payment_success.destination')}</Text>
                <Text w="50%" variant="regular" textAlign="left">{paymentDetails?.name}</Text>
              </HStack>
              {
              paymentDetails?.transferType === 'ach' || paymentDetails?.transferType === 'domesticWire' || paymentDetails?.transferType === 'internationalWire' ? (
                <>
                  {paymentDetails?.transferType === 'internationalWire'
                    ? (
                      <HStack className={style.card_info_item}>
                        <Text w="50%" variant="regular" className="text-light" textAlign="left">{t('payment_success.swiftCode')}</Text>
                        <Text w="50%" variant="regular" textAlign="left">{contactDetails?.wire?.international?.bankIdentifierCode}</Text>
                      </HStack>
                    ) : null }
                  <HStack className={style.card_info_item}>
                    <Text w="50%" variant="regular" className="text-light" textAlign="left">{t('payment_success.accountNumber')}</Text>
                    <Text w="50%" variant="regular" textAlign="left">{paymentDetails?.accountNumber}</Text>
                  </HStack>
                  {paymentDetails?.transferType !== 'internationalWire'
                    ? (
                      <HStack className={style.card_info_item}>
                        <Text w="50%" variant="regular" className="text-light" textAlign="left">{t('payment_success.routingNumber')}</Text>
                        <Text w="50%" variant="regular" textAlign="left">{paymentDetails?.routingNumber}</Text>
                      </HStack>
                    ) : null }
                  <HStack className={style.card_info_item}>
                    <Text w="50%" variant="regular" className="text-light" textAlign="left">{t('payment_success.bank')}</Text>
                    <Text w="50%" variant="regular" textAlign="left">{paymentDetails?.bankName}</Text>
                  </HStack>
                  <HStack className={style.card_info_item}>
                    <Text w="50%" variant="regular" className="text-light" textAlign="left">{t('payment_success.accountType')}</Text>
                    <Text w="50%" variant="regular" textAlign="left">{camel2title(paymentDetails?.accountType)}</Text>
                  </HStack>
                  {paymentDetails?.transferType === 'internationalWire'
                    ? (
                      <HStack className={style.card_info_item}>
                        <Text w="50%" variant="regular" className="text-light" textAlign="left">{t('payment_success.country')}</Text>
                        <Text w="50%" variant="regular" textAlign="left">{contactDetails?.wire?.international?.beneficiaryAddress?.country}</Text>
                      </HStack>
                    ) : null }
                </>
              ) : null
            }

              {
              paymentDetails?.transferType === 'solidCard' ? (
                <>
                  <HStack className={style.card_info_item}>
                    <Text w="50%" variant="regular" className="text-light" textAlign="left">{t('payment_success.phone')}</Text>
                    <Text w="50%" variant="regular" textAlign="left">{contactDetails?.phone}</Text>
                  </HStack>
                  <HStack className={style.card_info_item}>
                    <Text w="50%" variant="regular" className="text-light" textAlign="left">{t('payment_success.email')}</Text>
                    <Text w="50%" variant="regular" textAlign="left">{contactDetails?.email}</Text>
                  </HStack>
                </>
              ) : null
            }

              {
              paymentDetails?.transferType === 'physicalCheck' ? (
                <HStack className={style.card_info_item}>
                  <Text w="50%" variant="regular" className="text-light" textAlign="left">{t('payment_success.address')}</Text>
                  <Text w="50%" variant="regular" textAlign="left">{`${contactDetails?.check?.address.line1},${contactDetails?.check?.address.line2},${contactDetails?.check?.address.city} ${contactDetails?.check?.address.state} ${contactDetails?.check?.address.postalCode}`}</Text>
                </HStack>
              ) : null
            }
              <HStack className={style.card_info_item}>
                <Text w="50%" variant="regular" className="text-light" textAlign="left">{t('payment_success.description')}</Text>
                <Text w="50%" variant="regular" textAlign="left">{paymentDetails?.description}</Text>
              </HStack>
            </Box>
          </Box>
        </Status>
      </MiniBox>
    </div>
  );
}

export default PaymentSuccess;
